.goalForm {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin: 20px 0;

    &__submit {
        margin: 30px auto 0;

        &Btn {
            cursor: pointer;
            width: 100px;
            height: 30px;
            margin: 0 auto;
            color: #00ffdc;
            border: 0;
            outline: none;
            border-radius: 5px;
            background-color: rgb(29, 62, 78, 0.64);
        }
    }
}