@mixin block_style {
    height: inherit;
    width: 300px;
    flex-direction: column;
    border: 1px solid rgb(82, 107, 130);
}

@keyframes hide_btn {
    0% {
        margin-right: 0;
    }

    50% {
        opacity: 0.5;
    }

    100% {
        margin-right: -30px;
        opacity: 0;
    }
}

@keyframes turn_arrow {
    0% {
        transform: rotate(0);
    }

    100% {
        transform: rotate(-180deg);
    }
}

@keyframes open_section {
    0% {
        margin-right: -301px;
    }

    100% {
        margin-right: 0;
    }
}

.app {
    position: fixed; 
    z-index: 999;
    height: 62vh; 
    right: 10px;
    display: flex;
    align-items: center;

    &__extraBlock {
        @include block_style;
        background: linear-gradient(130deg, #0b2f5f , #276cc5 20%, #fff 80%); 
        border-radius: 10px;
        border-right: 0;
        margin-right: -301px;

        &Open {
            border-top-right-radius: 0;
            border-bottom-right-radius: 0;
            animation: open_section 1s;
            margin-right: 0;
        }
    }

    &__expandBtn {
        position: relative;
        background: #00ffdc;
        border-top-left-radius: 10px;
        border-bottom-left-radius: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 90px;
        cursor: pointer;

        &Open {
            > svg {
                animation: turn_arrow 0.5s;
                transform: rotate(-180deg);
            }
        }
    }

    &__mainBlock {
        display: flex;
        @include block_style;
        background: linear-gradient( 50deg, #fff 20%, #276cc5 80%, #0b2f5f); 
        border-radius: 10px;

        &Expanded {
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
        }
    }

    @media screen and (min-width: 380px) and (max-width: 900px) {
        top: 10px; 
    }


    @media screen and (max-width: 500px) {
        top: 50px; 

        @mixin block_style {
            max-height: 100vh;
        }
    }

    @media screen and (min-width: 1024px) {
        top: 50px;

        @mixin block_style {
            max-height: 65vh;
        }
    }
}