.goalItem {
    width: calc(100% - 24px);
    display: flex;
    flex-direction: column;
    margin: 20px 0 0;
    border: 2px solid rgb(29, 62, 78, 0.64);
    background-color: rgb(29, 62, 78, 0.2);
    border-radius: 10px;
    padding: 10px;

    &__name {
        color: #fff;
        margin: 0 auto 20px 0;
    }
}