@mixin goalWrap {
    display: flex;
    flex-direction: column;
    margin: 0 5%;
}

.goalBlock {
    &__addGoal {
        @include goalWrap;
    }

    &__goals {
        @include goalWrap;

        max-height: 53vh;
        // overflow-y: scroll;
        overflow-x: hidden;

        &::-webkit-scrollbar {
            width: 2px;
            background-color: rgb(67, 82, 90, 0.8);
        }
    
        &::-webkit-scrollbar-track {
            border-radius: 10px;
        }
    
        &::-webkit-scrollbar-thumb {
            background-color: #fff;
    
            &:hover {
                background-color: #9ab4fa;
            }
        }
    }
}

.addBtn {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 20px;

    > button {
        cursor: pointer;
        width: 100px;
        height: 30px;
        color: #00ffdc;
        border: 0;
        outline: none;
        border-radius: 5px;
        background-color: rgb(29, 62, 78, 0.64);
    }
}